.contacts {
  @include pageGrid;

  padding-bottom: 120px;

  @include onDesktop {
    padding-bottom: 160px;
  }

  &__title {
    grid-column: 1 / -1;

    text-align: center;
    font-weight: 600;
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -1.5px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    row-gap: 16px;

    grid-column: span 2;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }

    &-input {
      height: 48px;
      border-radius: 40px;
    }

    &-email {
      height: 48;
      border-radius: 40px;
    }

    &-textarea {
      height: 148px;
      resize: vertical;
      max-height: fit-content;
      border-radius: 16px;
    }

    &-button {
      margin-top: 16px;
      height: 56px;
      border-radius: 40px;
      cursor: pointer;
      text-align: center;

      &:hover {
        color: $main-text-color;
        background-color: $menu-main-color;
      }
    }
  }

  &__info {
    margin-top: 48px;
    display: flex;
    flex-direction: column;
    row-gap: 24px;

    grid-column: span 2;

    @include onTablet {
      margin-top: 0;
      grid-column: 4 / -1;
    }

    @include onDesktop {
      margin-top: 0;
      grid-column: 8 / -1;
    }

    &-title {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 19.6px;
    }

    &-text {
      margin: 0;
      margin-top: 8px;
      font-weight: 500;
      font-size: 16px;
      line-height: 22.4px;
    }
  }
}
